<template>
  <div>
    <van-nav-bar title="我的优惠卷" left-arrow @click-left="$router.push('/myjp')" />
    <div class="user-main" >
      <div class="get-coupon" @click = "$router.push('/exchangeCoupon')" >
        兑换优惠券
        <span></span>
      </div>

      <div class="empty-tips" v-show="bool">
        <img
          src="https://jp.juancdn.com/jpwebapp_v1/images_v1/empty_icons/user_coupon.png?529205e5-1&sv=449ce9ee"
          alt
        />
        <p>领券小贴士</p>
        <p class="sub-tips">活动领取、平台发放、券码兑换</p>
      </div>

      <van-row
        class="box-row"
        v-for="(item,index) in this.$store.state.myjp.useCoupon"
        :key="index"
      >
        <van-col span="18" class="col-18-box" :style="{background:item.color}">
          <div class="left">
            <van-icon name="bill-o" />
          </div>
          <div class="center">
            <p class="text">{{item.name}}</p>
            <p class="time">{{item.time}}</p>
          </div>
        </van-col>

        <van-col span="6" class="col-6-box" :style="{background:item.color}">
          <div class="box-right">
            <div class="price">
              ￥
              <span>{{item.valueDesc}}</span>
            </div>
            <div class="btn" @click="coun(index)">{{item.bool?'去使用':'已使用'}}</div>
          </div>
        </van-col>
      </van-row>
    </div>
    <p class="foot">copyright © 小明网络科技有限公司</p>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data(){
    return {
      bool:false
    }
  },
  methods: {
    coun(index) {
      if (this.$store.state.myjp.useCoupon[index].bool) {
        this.$router.push("/shoppingClass");
      } else {
        Toast("已经使用过啦");
      }
    }
  },
  created() {
    // console.log(this.$store.state.myjp.useCoupon);

    if (this.$store.state.myjp.useCoupon.length==0) {
        this.bool = true
    }


    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.bool = true;
  }
};
</script>

<style lang="scss" scoped>
.user-main {
  padding-top: 15px;
  background-color: #fff;
  box-sizing: border-box;
  min-height: 416px;
  box-sizing: border-box;
  p {
    margin: 0;
  }
  .get-coupon {
    margin: 0 12px;
    height: 48px;
    line-height: 48px;
    font-size: 16px;
    color: #333;
    background-color: #fff;
    border: 1px solid #dbdbdb;
    box-sizing: border-box;
    text-align: center;
    span {
      display: inline-block;
      width: 16px;
      height: 16px;
      border: 1px solid #666;
      border-radius: 50%;
      vertical-align: middle;
      position: relative;
      &::after {
        content: "+";
        display: block;
        position: absolute;
        top: -18px;
        left: 0px;
        font-size: 20px;
      }
    }
  }
}
.empty-tips {
  padding-top: 21px;
  padding-bottom: 48px;
  font-size: 16px;
  background-color: #fff;
  box-sizing: border-box;
  color: #333;
  text-align: center;
  img {
    display: block;
    margin: 0 auto;
    margin-bottom: 12px;
    width: 140px;
    height: auto;
  }
  p.sub-tips {
    margin-top: 4px;
    font-size: 12px;
    color: #999;
  }
}

.box-row {
  padding: 0 5px;
  height: 80px;
  margin: 10px 0;
}
.col-18-box {
  height: 100%;
  opacity: 0.6;
  background: #ff5001;
  color: #fff;
  .left {
    display: inline-block;
    width: 20%;
    height: 100%;
    font-size: 40px;
    line-height: 80px;
    text-align: center;
  }
  .center {
    display: inline-block;
    width: 80%;
    font-size: 12px;
    p {
      margin: 8px 0;
    }
  }
}
.col-6-box {
  background: #ff5001;
  height: 100%;
  position: relative;
  //  line-height: 80px;
}
.box-right {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.price {
  text-align: center;
  color: #fff;
  span {
    font-size: 18px;
  }
}
.btn {
  border: 1px solid #fff;
  padding: 2px;
  font-size: 12px;
  border-radius: 15px;
  color: #fff;
}
.foot {
  font-size: 12px;
  height: 14px;
  line-height: 14px;
  color: #bbb;
  font-weight: 400;
  margin: 0;
  text-align: center;
}
</style>